@media (max-width: 1440px) {
  .tools {

    .cover_tools {
      .first_tools {
        margin-bottom: 80px;
        display: flex;
        // justify-content: space-around;
        flex-wrap: wrap;

        .title_tools {
          width: 400px;
          color: #000000;
          font-size: 40px;
          padding: 20px;
          text-align: center;
          font-weight: bold;
          margin: auto;

          // margin-bottom: 80px;
          .title_tools_small {
            font-weight: lighter;
            color: #000000;
            font-size: 20px;
            padding-left: 20px;
            text-align: center;
            // font-weight: bold;

          }
        }

        .image_title_tools {
          width: 400px;
          height: 400px;

          .real_img {
            width: 400px;
            height: 400px;
          }
        }

      }



      .wrap_tools_right {

        // background-color: #7d889c;
        width: 97%;
        margin: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .wrap_tools_part_blue {
          width: 13%;

          // margin: 30px;
          .wrap_image_tools2 {
            background-color: #29B6F6;
            align-items: center;
            padding:10px;
            // padding:10px; border-radius: 50%;
            // width: 100%;
