              margin: auto;
              align-items: center;
              justify-content: center;
              // border-radius: 50%;
              width: 120px;
              height: 120px;
            }

            .wrap_ww_tools2 {

              text-align: center;

              .write_tools2 {
                font-weight: bold;
                text-align: center;
                font-size: 20px;
                color: rgb(255, 255, 255);
              }

              .write_tools22 {
                text-align: center;
                font-size: 20px;
                color: rgb(255, 255, 255);
              }
            }
          }


        }

        .wrap_tools_part_yellow {
          width: 14%;

          // margin: 30px;
          .wrap_image_tools1 {
            background-color: #F1C40F;
