            }

            .wrap_ww_tools1 {

              text-align: center;

              .write_tools1 {
                font-weight: bold;
                text-align: center;
                font-size: 20px;
                color: rgb(255, 255, 255);
              }

              .write_tools21 {
                text-align: center;
                font-size: 20px;
                color: rgb(0, 0, 0);
              }
            }
          }


        }

        .wrap_tools_part_grey {
          width: 14%;

          // margin: 30px;
          .wrap_image_tools3 {
            background-color: #E0E0E0;
            align-items: center;
 padding:10px;
