.framework {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;

  background-color: rgb(236, 243, 243);
 
  min-height: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  object-fit: contain;
  background: cover;
  margin-bottom: 50px !important;
  padding-bottom: 50px;

  .cover_frame {
    max-width: 1440px !important;
    margin: auto;
    width: 100%;

    .title_frame {
      color: #000000;
      font-size: 35px;
      padding: 70px;
      // width: 700px;
      text-align: center;
      font-weight: bold;

      .small_title_frame {
        font-size: 20px;
        text-align: center;
        font-weight: normal;
        margin: auto;
        max-width: 400px;
      }
    }

    .frame {
      margin: auto;
      width: 60%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;


      // float: right;
      // justify-content: center;
      .frame_image1 {
      
       
        border-radius: 50%;
        width: 300px;
        height: 300px;
        margin: auto;

        .image1 {
         
          border: solid 7px #ff7b00;
          ;
          border-radius: 50%;
          width: 200px;
          height: 200px;
        }

        .triangle-left {
          position: absolute;
          margin-left: -90px;
          margin-top: -150px;
          width: 0;
          height: 0;
          border-top: 30px solid transparent;
          border-right: 100px solid #ff7b00;
          border-bottom: 30px solid transparent;
        }

        .triangle-right {
          position: absolute;
          margin-left: 206px;
          margin-top: -150px;
          width: 0;
          height: 0;
          border-top: 30px solid transparent;
          border-left: 100px solid #ff7b00;
          border-bottom: 30px solid transparent;
        }
      }

      .image_wrap_frame {
        border-radius: 15px;
        margin: auto;
        border: solid 4px #F1C40F;
        background-color: #ffffff;
        padding: 10px;
        width: 200px;
        height: 200px;
        align-items: center;

        .image_frame {
          align-items: center;
          margin: auto;
          width: 120px;
          height: 120px;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          margin: auto;
        }

        .wrap_ww_frame {

          // margin-top: -20px;
          .write_frame {
            text-align: center;
            font-size: 20px;
            color: rgb(0, 0, 0);
          }

          .write_frame2 {
            text-align: center;
            font-size: 20px;
            color: rgb(0, 0, 0)
          }
        }

        &:hover {
          background-color: rgb(112, 155, 248);
          color: rgb(0, 0, 0);
          filter: brightness(90%);
        }
      }

    }

    // @keyframes fade-in {
    //   to {
    //     scale: .6;
    //     opacity: 0;
    //   }

    // }
  }
}

@import'./responsive/frameRes.scss'