.menu1 {
  background-color: #ffffff;
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 70px;
  background-color: rgb(255, 255, 255);
  box-shadow: 2px 2px 5px rgb(77, 71, 71);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;

  .cover_menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1440px !important;
    width: 100%;

    .logo {
      border-radius: 50%;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background-color: aqua;

      .logo_img {
        border-radius: 50%;
        width: 70px;
        height: 70px;
        object-fit: cover;
      }
    }


    .cover_menu1 {

      align-items: center;
      margin: auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 85%;

      .write1_menu1 {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        font-size: 20px;


        .writeM1 {

          padding: 20px;
          align-items: center;
          color: rgb(0, 0, 0);
          text-decoration: none;

          &:hover {
            display: block;
            // background-color: rgb(255, 108, 199);
            color: rgb(0, 0, 0);
            border-bottom: 3px solid rgb(250, 50, 150);
            font-weight: bold;
          }
        }

      }

      .write2_menu1 {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        font-size: 20px;

        .writeM2 {
          padding: 20px;
          align-items: center;
          color: rgb(0, 0, 0);
          text-decoration: none;

          &:hover {
            display: block;
            // background-color: rgb(255, 108, 199);
            color: rgb(0, 0, 0);
            border-bottom: 3px solid rgb(250, 50, 150);
            font-weight: bold;
          }
        }

      }
    }
  }

}